import utils from '~/common/utils'
import Comment from '~/components/Comment'

export default {
  components: {
    Comment
  },
  async asyncData({ $axios, params, error }) {
    let topic
    try {
      topic = await $axios.get('/api/topic/' + params.id)
    } catch (e) {
      error({
        statusCode: 404,
        message: '话题不存在'
      })
      return
    }

    const [favorited, commentsPage, likeUsers] = await Promise.all([
      $axios.get('/api/favorite/favorited', {
        params: {
          entityType: 'topic',
          entityId: params.id
        }
      }),
      $axios.get('/api/comment/list', {
        params: {
          entityType: 'topic',
          entityId: params.id
        }
      }),
      $axios.get('/api/topic/recentlikes/' + params.id)
    ])

    topic.extraData = JSON.parse(topic.extraData)
    return {
      topic,
      commentsPage,
      favorited: favorited.favorited,
      likeUsers
    }
  },
  data() {
    return {
      href: ''
    }
  },
  computed: {
    isOwner() {
      return (
        this.$store.state.user.current &&
        this.topic &&
        this.$store.state.user.current.id === this.topic.user.id
      )
    }
  },
  mounted() {
    this.href = window.location.href
    // console.log('topic:' + JSON.stringify(this.topic))
  },
  updated() {
    this.loading()
  },
  methods: {
    loading() {
      if (!this.$store.state.user.current) {
        const loader = this.$loading.show(
          {
            container: this.$refs.loadingContainer,
            // isFullPage: false,
            canCancel: false
            // backgroundColor: 'blue'
          },
          {
            default: this.$createElement('div', ['登录后才能查看'])
          }
        )
      }
    },
    onCopy() {
      this.$toast.success('复制成功！可以粘贴分享啦')
    },
    shareContent() {
      return (
        '单身交友 —— 本人性别：' +
        this.topic.extraData.me_gender +
        '\n  ' +
        ' 家乡：' +
        this.topic.extraData.me_city +
        ' 年龄：' +
        this.topic.extraData.me_age +
        ' 工作单位：' +
        this.topic.extraData.me_work_company +
        '\n ——其他详情请点击链接：' +
        this.href
      )
    },
    async addFavorite(topicId) {
      try {
        if (this.favorited) {
          await this.$axios.get('/api/favorite/delete', {
            params: {
              entityType: 'topic',
              entityId: topicId
            }
          })
          this.favorited = false
          this.$toast.success('已取消收藏！')
        } else {
          await this.$axios.get('/api/topic/favorite/' + topicId)
          this.favorited = true
          this.$toast.success('收藏成功')
        }
      } catch (e) {
        console.error(e)
        this.$toast.error('收藏失败：' + (e.message || e))
      }
    },
    async deleteTopic(topicId) {
      if (process.client && !window.confirm('是否确认删除该话题？')) {
        return
      }
      try {
        await this.$axios.post('/api/topic/delete/' + topicId)
        this.$toast.success('删除成功', {
          duration: 2000,
          onComplete() {
            utils.linkTo('/topics')
          }
        })
      } catch (e) {
        console.error(e)
        this.$toast.error('删除失败：' + (e.message || e))
      }
    },
    async like(topic) {
      try {
        await this.$axios.post('/api/topic/like/' + topic.topicId)
        topic.liked = true
        topic.likeCount++
        this.likeUsers = this.likeUsers || []
        this.likeUsers.unshift(this.$store.state.user.current)
      } catch (e) {
        if (e.errorCode === 1) {
          this.$toast.info('请登录后点赞！！！', {
            action: {
              text: '去登录',
              onClick: (e, toastObject) => {
                utils.toSignin()
              }
            }
          })
        } else {
          topic.liked = true
          this.$toast.error(e.message || e)
        }
      }
    }
  },
  head() {
    return {
      title: this.$siteTitle(this.topic.title)
    }
  }
}
