import Cookies from 'js-cookie'
class Utils {
  getCity() {
    // Cookies.get('city')
    const city = Cookies.get('city')
    if (city !== undefined) {
      return city
    }
    return '北京'
  }

  setCity(city) {
    return Cookies.set('city', city)
  }

  linkTo(path) {
    window.location = path
    // 这里使用$router.push会导致跳转页面之后window.vditor对象undefined，原因未知
    // window.$nuxt.$router.push(path)
  }

  toSignin(ref) {
    if (!ref && process.client) {
      // 如果没配置refUrl，那么取当前地址
      ref = window.location.pathname
    }
    this.linkTo('/user/signin?ref=' + encodeURIComponent(ref))
  }

  isSigninUrl(ref) {
    return ref === '/user/signin'
  }

  isArray(sources) {
    return Object.prototype.toString.call(sources) === '[object Array]'
  }

  isDate(sources) {
    return (
      {}.toString.call(sources) === '[object Date]' &&
      sources.toString() !== 'Invalid Date' &&
      !isNaN(sources)
    )
  }

  isElement(sources) {
    return !!(sources && sources.nodeName && sources.nodeType === 1)
  }

  isFunction(sources) {
    return Object.prototype.toString.call(sources) === '[object Function]'
  }

  unique(arr) {
    return arr.filter((item, index, arr) => {
      return arr.indexOf(item, 0) === index
    })
  }

  isNumber(sources) {
    return (
      Object.prototype.toString.call(sources) === '[object Number]' &&
      isFinite(sources)
    )
  }

  isObject(sources) {
    return Object.prototype.toString.call(sources) === '[object Object]'
  }

  isString(sources) {
    return Object.prototype.toString.call(sources) === '[object String]'
  }

  isBoolean(sources) {
    return typeof sources === 'boolean'
  }

  getCityList() {
    return ['北京', '上海', '深圳', '武汉', '杭州']
  }

  isEmpty(content) {
    const str = content
    if (str === '' || str === undefined) return true
    const regu = '^[ ]+$'
    const re = new RegExp(regu)
    return re.test(str)
  }

  getTopText(topType) {
    if (topType === 1) {
      return '顶'
    }

    if (topType === 2) {
      return '火'
    }

    if (topType === 3) {
      return '精华'
    }
    return ''
  }

  formatDate(timestamp, fmt) {
    fmt = fmt || 'yyyy-MM-dd HH:mm:ss'
    const date = new Date(timestamp)
    const o = {
      'M+': date.getMonth() + 1,
      'd+': date.getDate(),
      'h+': date.getHours() % 12,
      'H+': date.getHours(),
      'm+': date.getMinutes(),
      's+': date.getSeconds(),
      'q+': Math.floor((date.getMonth() + 3) / 3),
      S: date.getMilliseconds()
    }
    if (/(y+)/.test(fmt)) {
      fmt = fmt.replace(
        RegExp.$1,
        (date.getFullYear() + '').substr(4 - RegExp.$1.length)
      )
    }
    for (const k in o) {
      if (new RegExp('(' + k + ')').test(fmt)) {
        fmt = fmt.replace(
          RegExp.$1,
          RegExp.$1.length === 1
            ? o[k]
            : ('00' + o[k]).substr(('' + o[k]).length)
        )
      }
    }
    return fmt
  }

  prettyDate(timestamp) {
    const minute = 1000 * 60
    const hour = minute * 60
    const day = hour * 24
    const diffValue = new Date().getTime() - timestamp
    if (diffValue / minute < 1) {
      return '刚刚'
    } else if (diffValue / minute < 60) {
      return parseInt(diffValue / minute) + '分钟前'
    } else if (diffValue / hour <= 24) {
      return parseInt(diffValue / hour) + '小时前'
    } else if (diffValue / day <= 30) {
      return parseInt(diffValue / day) + '天前'
    }
    return Utils.formatDate(timestamp, 'yyyy-MM-dd HH:mm:ss')
  }
}
export default new Utils()
